.trademarks__title, .products__title, .scheme__title {
  text-transform: uppercase;
  font-style: normal;
  font-weight: 400;
  font-size: 60px;
  line-height: 120%;
  position: relative;
  padding-top: 30px;
}

.trademarks__title::before, .products__title::before, .scheme__title::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  background: #5C8EE6;
  height: 1px;
  width: 150px;
}

* {
  padding: 0;
  margin: 0;
  border: 0;
}

*,
*::before,
*::after {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

aside,
nav,
footer,
header,
section {
  display: block;
}

body {
  line-height: 1;
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

input::-ms-clear {
  display: none;
}

button {
  cursor: pointer;
}

button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

a,
a:visited {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

ul li {
  list-style: none;
}

img {
  vertical-align: top;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: inherit;
  font-size: inherit;
}

.container {
  max-width: 1474px;
  width: 100%;
  margin: 0 auto;
  padding: 0 5px;
}
@media (max-width: 1199.98px) {
  .container {
    max-width: 1160px;
  }
}
@media (max-width: 991.98px) {
  .container {
    max-width: 970px;
  }
}
@media (max-width: 767.98px) {
  .container {
    max-width: 740px;
  }
}
@media (max-width: 479.98px) {
  .container {
    max-width: none;
  }
}

.container-fluid {
  max-width: 1930px;
  width: 100%;
  margin: 0 auto;
}
@media (max-width: 1199.98px) {
  .container-fluid {
    max-width: 1160px;
  }
}
@media (max-width: 991.98px) {
  .container-fluid {
    max-width: 970px;
  }
}
@media (max-width: 767.98px) {
  .container-fluid {
    max-width: 740px;
  }
}
@media (max-width: 479.98px) {
  .container-fluid {
    max-width: none;
  }
}

body {
  font-family: "Open Sans", sans-serif;
  color: #292D33;
}
body.locked {
  overflow: hidden;
}

.btn {
  cursor: pointer;
  margin: 0;
  padding: 25px 60px;
  font-weight: 600;
  font-size: 16px;
  display: inline-block;
  color: #fff;
}
.btn-line {
  font-weight: 600;
  font-size: 16px;
  line-height: 160%;
  color: #5C8EE6;
  position: relative;
  padding-left: 60px;
  transition: 0.3s;
}
.btn-line::before {
  content: "";
  position: absolute;
  left: 0;
  top: 50%;
  height: 1px;
  width: 40px;
  background: #5C8EE6;
  transition: 0.3s;
}
.btn-line:hover {
  padding-left: 80px;
}
.btn-line:hover::before {
  width: 60px;
}
.btn-primary {
  background-color: #5C8EE6;
  transition: 0.3s;
}
.btn-primary:hover {
  background-color: #3A71D1;
}

@keyframes fade {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
nav {
  overflow: hidden;
  position: relative;
  min-height: 93px;
}

.navbar {
  display: flex;
  width: 100%;
  justify-content: space-between;
  align-items: center;
  padding-left: 50px;
  border-bottom: 1px solid rgba(235, 235, 240, 0.2);
  border-left: 1px solid rgba(235, 235, 240, 0.2);
  position: relative;
}

.logo {
  position: relative;
  display: flex;
  align-items: center;
}
.logo a {
  color: white;
  margin-right: 25px;
}

.divider {
  width: 1px;
  min-height: 92px;
  background: rgba(235, 235, 240, 0.2);
}

.menu {
  display: flex;
  margin-left: -10px;
  align-items: center;
  justify-content: space-between;
  gap: 25px;
}
.menu__item-link {
  font-weight: 400;
  font-size: 14x;
  color: white;
  padding-bottom: 10px;
}
.menu__item-link:hover {
  border-bottom: 1px solid #5C8EE6;
}

.navbar__controls {
  display: flex;
  margin-left: 60px;
  align-items: center;
  justify-content: space-between;
  color: #fff;
  font-weight: 600;
  gap: 20px;
}
.navbar__phone-number {
  font-style: normal;
  color: inherit;
  font-weight: 600;
  font-size: 16px;
  line-height: 160%;
  padding-left: 25px;
  position: relative;
}
.navbar__phone-number::after {
  content: "";
  width: 16px;
  height: 16px;
  position: absolute;
  left: 0;
  top: 5px;
  background: url(../img/phone.svg);
}
.navbar__btn {
  padding: 38px 30px;
  height: 100%;
}

.about {
  color: #FFFFFF;
  padding-top: 150px;
  max-width: 1080px;
  margin-bottom: 50px;
}
.about__wrapper {
  background-image: url(../img/bg.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  min-height: 970px;
  position: relative;
}
.about__title {
  font-style: normal;
  text-transform: uppercase;
  font-weight: 400;
  font-size: 50px;
  line-height: 120%;
  padding-top: 30px;
  margin-bottom: 30px;
  position: relative;
}
.about__title::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 150px;
  height: 1px;
  background: #5C8EE6;
}
.about__text {
  margin-bottom: 50px;
  max-width: 720px;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 160%;
}

.advantages {
  margin-top: 100px;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  color: #fff;
}
.advantages__items {
  display: flex;
}
.advantages__item {
  padding: 30px 50px;
  border-top: 1px solid rgba(235, 235, 240, 0.2);
  border-left: 1px solid rgba(235, 235, 240, 0.2);
}
.advantages__item:last-child {
  border-right: 1px solid rgba(235, 235, 240, 0.2);
}
.advantages__item-img {
  margin-bottom: 24px;
}
.advantages__item-text {
  font-weight: 400;
  font-size: 16px;
  line-height: 160%;
}

.burger {
  display: none;
  cursor: pointer;
}

@media screen and (max-width: 991.98px) {
  .menu__item {
    margin-top: 15px;
  }
  .menu__item-link {
    color: white;
  }
}
.scheme {
  padding-top: 150px;
  padding-bottom: 75px;
  color: rgb(41, 45, 51);
}
.scheme__title {
  margin-bottom: 50px;
}
.scheme__stages {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 30px;
}
.scheme__stage-item {
  position: relative;
  max-width: 330px;
  height: 267px;
}
.scheme__stage-item::after {
  content: "";
  background: #EBEBF0;
  height: 1px;
  width: 260px;
  position: absolute;
  right: 0;
  top: 30px;
}
.scheme__stage-number {
  display: block;
  color: #5C8EE6;
  font-style: normal;
  font-weight: 400;
  font-size: 48px;
  line-height: 120%;
  margin-bottom: 30px;
}
.scheme__stage-title {
  font-weight: 400;
  font-weight: 400;
  font-size: 28px;
  line-height: 160%;
  color: #292D33;
  margin-bottom: 10px;
}
.scheme__stage-description {
  margin-bottom: 15px;
  font-weight: 400;
  font-size: 16px;
  line-height: 160%;
  color: #828A99;
}

.products {
  padding-top: 75px;
  padding-bottom: 75px;
}
.products__title {
  margin-bottom: 50px;
}
.products__list {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
}
.products__list-item {
  padding: 40px 30px 0 50px;
  border: 1px solid #EBEBF0;
  transition: 0.3s;
  height: 600px;
  background-color: #fff;
  position: relative;
  z-index: 1;
}
.products__list-item:hover {
  border: 1px solid #5C8EE6;
  z-index: 2;
  transform: scale(1.2);
}
.products__item-title {
  font-weight: 400;
  font-size: 28px;
  line-height: 26px;
  color: #292D33;
  margin-bottom: 10px;
}
.products__item-texts {
  font-weight: 400;
  font-size: 16px;
  line-height: 26px;
  color: #828A99;
  padding-bottom: 10px;
}
.products__item-img {
  display: inline-block;
  position: absolute;
  bottom: 0;
  right: 0;
}

.trademarks {
  padding-top: 75px;
  padding-bottom: 75px;
}
.trademarks__title {
  margin-bottom: 50px;
}
.trademarks__list {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
}
.trademarks__list-item {
  height: 280px;
  border: 1px solid #EBEBF0;
  transition: 0.3s;
  display: flex;
  padding: 50px 50px 30px 70px;
}
.trademarks__list-item.active {
  border: 1px solid #5C8EE6;
}
.trademarks__item-info {
  max-width: 360px;
}
.trademarks__item-img {
  margin-right: 70px;
  transition: 0.3s;
}
.trademarks__item-img.ag {
  background-image: url(../img/trademarks/1.svg);
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  width: 140px;
  height: 120px;
}
.trademarks__item-img.ag.active {
  background-image: url(../img/trademarks/1-hover.svg);
}
.trademarks__item-img.ap {
  background-image: url(../img/trademarks/2.svg);
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  width: 102px;
  height: 140px;
}
.trademarks__item-img.ap.active {
  background-image: url(../img/trademarks/2-hover.svg);
}
.trademarks__item-title {
  font-size: 28px;
  line-height: 160%;
  color: #292D33;
  margin-bottom: 10px;
  transition: 0.3s;
}
.trademarks__item-title.active {
  color: #5C8EE6;
}
.trademarks__item-text {
  font-size: 16px;
  line-height: 160%;
  color: #828A99;
}